        p.mb-0 La primera etapa de <strong> la fase de diseo comprende al menos cuatro partes</strong>, a saber: el anlisis de actores involucrados, la identificacin de problemas, el anlisis de problemas (causa-efecto) y el anlisis de objetivos; sin embargo, desde la experiencia, es altamente recomendable efectuar un anlisis consultivo a partir de informacin secundaria y un ejercicio previo al anlisis de problemas que de manera prctica es un valioso apoyo para la identificacin del problema central y de paso al anlisis de causas de este, la matriz de Vester. Adicionalmente, atendiendo las necesidades de la ruralidad y el sector productivo agropecuario, una recomendacin importante es robustecer el diagnstico de la situacin problmica a travs de la realizacin de un diagnstico rural participativo <strong>(DRP).</strong>

