    .row.align-items-center.my-5
      .col-lg-8.mb-2(data-aos="zoom-in-right")
        p La herramienta del ejemplo anterior es susceptible de mejora aplicando, por ejemplo, la variable de expectativa fuerza, ampliando el espectro de la herramienta hasta especificar las estrategias (otra columna) que se emplearn para la mejora de la participacin o la mitigacin de efectos adversos y como se mencion, incluso para la resolucin de conflictos entre las partes involucradas o al interior de estas. Existen otras formas de evaluar la participacin de los involucrados y que se pueden emplear en conjunto o en sustitucin del anlisis expectativa fuerza: <strong>poder-inters, influencia-impacto, poder-influencia, etc.</strong>
        .row.align-items-center.grad1.p-3
          .col-lg-2
            figure
