          .col-lg-7
            h4.text-light Anlisis de Involucrados
            .text-light.mb-0(style="font-size: 13px") Para la revisin de una herramienta ms completa de anlisis de involucrados incluyendo la aplicacin de la matriz de expectativa-fuerza con ejemplos ilustrativos, dirjase a:
