    #t_2_4.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.4 Diagnstico de la situacin problmica
    
    .row.align-items-center.my-4
      .col-lg-4.mb-2.col-md-8.mx-auto
        figure
          img(src='@/assets/curso/temas/tema2/img38.png', alt='Imagen decorativa')
      .col-lg-8.mb-2.my-3
        .cajon.color-primario.p-4(data-aos="slide-down",style="background: #CCE9E5")
          p.mt-0 Muchas fuentes de informacin acerca del mtodo EML usan un ejemplo recurrente de aplicacin de la herramienta de <strong>lluvia de ideas</strong> (<em>brainstorming</em>) con el protagonismo de la misma poblacin que es perfectamente vlido, pero que se puede fortalecer por medio de la indagacin del contexto de la problemtica identificada, previamente al ejercicio de definicin del problema central.
        
        p.mt-3 La acertada formulacin del proyecto y el mismo xito de la intervencin dependen en gran medida de la adecuada identificacin del problema, es por esto que se deben dedicar <strong>esfuerzos proporcionales en la etapa de diagnstico.</strong> El principal referente de este documento, Ortegn et.al (2005), aclara que la planificacin nace de <strong>la percepcin que se tiene de la problemtica</strong> y que se puede dar en concordancia con los siguientes aspectos:
    .row.justify-content-center
