                  img(src='@/assets/curso/temas/tema2/img43.svg',  alt='' style='max-width:170px; max-height:170px; margin: auto')
              
            p.text-center Condiciones de vida deficitarias
          .col-md.tarjeta.color-acento-botones.p-5(data-aos='fade-down', style='background: #FFE9DB')
            .row.justify-content-center.mb-4
              .col-lg-10.m-auto
                figure
                  img(src='@/assets/curso/temas/tema2/img44.svg',  alt='' style='max-width:170px; max-height:170px; margin: auto')
              
            p.text-center Efectos de la globalizacin de la economa y de tratados comerciales internacionales
    
    p.my-4.text-bold.text-center Adems, en la actualidad podemos identificar circunstancias problemticas en torno a:

    row.my-4(data-aos="zoom-in-up")
      div.p-5.BGM02.fondo-degradado-2
        ul(style="list-style-position: outside; padding-left: 1.2em; text-indent: -0.6em;")
            li.my-1(data-aos="fade-right", style="list-style-type: disc; color: #02DD84; font-size: 1.2em;")
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") <strong>Impactos ambientales adversos </strong> de las actividades productivas en todos los sectores de la economa.
            li.my-1(data-aos="fade-right", style="list-style-type: disc; color: #02DD84; font-size: 1.2em;")
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") <strong>Efectos directos o indirectos </strong> del conflicto armado o del dominio territorial de grupos armados.
            li.my-1(data-aos="fade-right", style="list-style-type: disc; color: #02DD84; font-size: 1.2em;")
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") <strong>Efectos regresivos</strong> de polticas de desarrollo o coyunturales.
            li.my-1(data-aos="fade-right", style="list-style-type: disc; color: #02DD84; font-size: 1.2em;")
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") <strong>Consecuencias socioeconmicas</strong> derivadas de la pandemia por el virus Sars-COV-2.
            li.my-1(data-aos="fade-right", style="list-style-type: disc; color: #02DD84; font-size: 1.2em;")
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") <strong>Aspectos inherentes a los efectos acumulados por dcadas</strong> en cuanto a la concentracin de la riqueza y la tierra, la pauperizacin del campesinado, el envejecimiento de la poblacin rural, la migracin masiva hacia las urbes, el deterioro del tejido social en la ruralidad, el despojo de la tierra, la capacidad productiva, los derechos y hasta los valores de la cultura rural, el desvanecimiento del relevo y empalme generacional, el rezago educativo para la poblacin rural, el escaso acceso a los factores productivos y la tecnologa para la productividad, la ausencia del estado y los programas sociales de los gobiernos en especial en la periferia, la inseguridad alimentaria y el hambre y malnutricin derivados de sta, la inequidad de gnero, el deterioro del suelo aprovechable, los efectos del cambio climtico sobre los ecosistemas por causas antrpicas, la huella de carbono generada por actividades como la ganadera extensiva, la contaminacin de fuentes de agua, la ampliacin de la frontera agrcola hacia zonas de reserva protegidas, el debilitamiento de la ciudadana participativa, la lentitud en la migracin hacia fuentes de energa renovables, la escasa infraestructura productiva para las actividades agropecuarias, entre otros.


    Separador
    #t_2_5.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.5 Identificacin de problemas a partir de informacin primaria

    .row.align-items-center 
      .col-lg-8.my-2
        p(data-aos="zoom-in-right") La identificacin de problemas por parte de la misma comunidad afectada no solo es importante para la adecuada formulacin de un plan de intervencin exitoso, <strong>la participacin de la poblacin en el proceso de desarrollo es importante para la sostenibilidad de los procesos de mejora en las actividades productivas</strong>, ya que muchas veces el fracaso de los proyectos se atribuye a la falta de sentido de pertenencia de las comunidades con estos, porque algunas veces se hacen intervenciones sin contar siquiera con su opinin al respecto.
        .cajon.color-terciario.p-4(style="background: #D9F3FF")
          p.my-4 El paradigma del desarrollo desde abajo viene a contrarrestar las polticas de intervencin desde los dirigentes nacionales y grandes actores internacionales <strong>(BM, FMI, tesoro nacional de USA, etc.)</strong> que a partir de los aos 80 vienen fomentando la aplicacin de polticas derivadas de la receta neoliberal (menor injerencia del estado y mayor orientacin del mercado en las dinmicas socioeconmicas) producto del llamado consenso de Washington, con un <strong>rotundo fracaso en trminos de impacto ambiental y aumento de la brecha social a escala continental.</strong>  
      .col-lg-4.my-2.col-md-8.mx-auto.my-3
        figure
          img(data-aos="zoom-in-left",src='@/assets/curso/temas/tema2/img45.png',  alt='')

    p(data-aos="zoom-in-right").my-4 El extensionismo agropecuario, actividad que data del <strong>siglo XIX en Estados Unidos y Europa como una iniciativa de atencin a la ruralidad desde los gobiernos con el apoyo del sector privado</strong>, especialmente el productor de tecnologa, ha logrado avances muy interesantes y exitosos en el diseo y ejecucin de procesos de intervencin integrales ms que asistenciales. Uno de los casos destacados en Colombia y la regin es la <strong>Federacin Nacional de Cafeteros</strong>. Producto de estas experiencias fortalecidas se encuentran metodologas centradas en la participacin de la poblacin, como el <strong>Diagnstico Rural Participativo (DRP)</strong> y la planificacin comunitaria, entre otros ejercicios como los realizados por el <strong>Servicio Nacional de Aprendizaje (SENA)</strong> en sus modelos de intervencin hechos hasta la dcada de los aos 90 (por efectos de las polticas coyunturales) a partir de los programas <strong>Capacitacin para la Participacin Campesina (CAPACA),  capacitacin empresarial campesina, la capacitacin para la organizacin y participacin comunitaria, programa de extensin rural del convenio SENA-Gobierno de Holanda</strong> y el trabajo  <strong>SENA-Escuela Nacional de Formacin Campesina ANUC-ENFOCA</strong>, estrategias con productos que se pueden consultar en los repositorios institucionales. 
    .row.justify-content-center
      hr.mt-3.justify-content-center(style="background:#FF9751; width: 80%; height: 2px")

    .row.align-items-center.mb-4
      .col-lg-10.mx-auto.mb-auto
        .bloque-texto-b.py-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            p El DRP, es una actividad participativa que permite la identificacin de los principales problemas tcnicos productivos, sociales y de organizacin para lograr la convivencia con los productores. Con el fin de conocer ms de cerca los diferentes aspectos de la comunidad, generando la informacin bsica para la elaboracin de los planes de accin comunitaria
            h3  (DNP, 2016, p.15).
            i.fas.fa-quote-right
            

    .row.my-3
      .col-lg-9.crd.crd--avatarHorizontal-left-borde-tarjeta.py-3.mb-3.m-auto(data-aos="zoom-in-right")
        .row.align-items-center
          .col-auto(style="z-index:1")
            figure
              img(src="@/assets/curso/temas/tema2/img46.svg", alt="", width= "100px", height="100px")
          .col(style="z-index:1")
            p.m-0 El <strong>DRP</strong> es una metodologa recomendable para la <strong>autogestin del desarrollo de las comunidades, lo que supone tambin la autodeterminacin, un valor importante de resaltar</strong>. Posee de manera inherente ventajas que dependen directamente del grado de participacin de los involucrados, entre las que se encuentran la <strong>eficacia en el levantamiento de datos de campo, la verificabilidad, confiabilidad, profundidad y suficiencia de la informacin obtenida.</strong>
    
    p.my-4 Tambin ayuda a sensibilizar y motivar la participacin de la poblacin ante la posible intervencin, fomenta la creatividad en las propuestas de solucin y puede conllevar un alto impacto de las propuestas respetando la cultura y la forma de ser de la poblacin, as como complementariedad con la informacin obtenida de fuentes secundarias. Algunos aspectos a tener en cuenta son, por un lado, la necesidad de <strong>un equipo multidisciplinario que planifique el diagnstico</strong> y por otro de <strong>recursos y tiempo para el trabajo de campo.</strong> Segn Expsito (2003), el DRP consta de siete pasos para su realizacin: 

    AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--beige")
      .row.align-items-center(titulo="1. Delimitar la poblacin objetivo")
        .col-lg-8.mb-4
          p Definir el alcance y el objetivo del diagnstico que puede ser a tres niveles, poblacin ms o menos dispersa, organizaciones rurales o empresas agropecuarias y agroindustriales propiamente dichas.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img47.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
      .row.align-items-center(titulo="2. Seleccionar y preparar el equipo facilitador")
        .col-lg-8.mb-4
          p El equipo de trabajo debe ser multidisciplinario, pues las poblaciones y sus problemas son de naturaleza multidimensional, y por ende, el enfoque de las actividades de extensin agropecuaria se considera integral, correspondiente con el marco normativo y las necesidades de fortalecimiento de la ruralidad colombiana; adems, el trabajo con poblaciones requiere de competencias en el manejo de este tipo de poblacin, as como de una clara vocacin de servicio.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img48.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
      .row.align-items-center(titulo="3. Anlisis de participantes")
        .col-lg-8.mb-4
          p Identificar y caracterizar a todos los participantes para establecer las mejores prcticas y herramientas posibles, adaptando la estrategia de comunicacin a las condiciones reales de los sujetos de la intervencin e identificando los liderazgos que pueden ayudar a la eficacia del proceso.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img49.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
      .row.align-items-center(titulo="4. Anlisis de expectativas")
        .col-lg-8.mb-4
          p Comunicar las expectativas de todos los actores ayudar a establecer metas comunes que motivan la participacin y la intencin para generar cambios en especial si tienen que ver con la calidad de vida de la poblacin. Aqu hay que prestar una especial atencin a cuestiones de gnero.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img50.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
      .row.align-items-center(titulo="5. Identificacin de las necesidades de informacin")
        .col-lg-8.mb-4
          p La informacin, su calidad, precisin, especificidad, etc. debe ser suficiente para que no queden vacos en el momento de procesarla durante la formulacin del proyecto y el planteamiento de las alternativas de intervencin. Esta informacin debe corresponder a la realidad rural, las estructuras sociales, las relaciones de gnero, familiares y dems condiciones culturales y particulares de la poblacin objetivo.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img51.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
      .row.align-items-center(titulo="6. Seleccin de las herramientas de investigacin")
        .col-lg-8.mb-4
          p La seleccin de herramientas debe obedecer no solo a las necesidades de informacin orientada a la formulacin del proyecto, sino tambin a las preferencias de la misma poblacin. Tambin es importante considerar la informacin desagregada por gnero de la poblacin en situacin de vulnerabilidad y discapacidad, de minoras, etc.; es decir, con un enfoque diferencial. Tambin se recomienda revisar informacin ya obtenida en otros procesos. Existe un conjunto muy amplio de herramientas participativas que se han diseado precisamente debido a las condiciones de la poblacin rural, uno de los campos de accin de la andragoga.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img52.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
      .row.align-items-center(titulo="7. Disear y planificar el proceso de diagnstico")
        .col-lg-8.mb-4
          p El xito del proceso de diagnstico, al igual que el xito del proyecto de intervencin, depende en gran medida de una buena planificacin. Se requiere establecer el tamao, las caractersticas del equipo de trabajo, el cronograma de actividades, la localizacin, medios de transporte, los materiales y otros recursos requeridos, etc.
        .col-lg-4
          figure
            img(src='@/assets/curso/temas/tema2/img53.svg', alt='Texto que describa la imagen', style='max-width:170px; max-height:170px; margin: auto')
    .row.mb-5
      .col-12 
        .fondo-degradado-2.p-4
          p.my-4.text-center.text-bold La informacin de primera mano es muy valiosa y requiere de un recurso metodolgico que puede ser el DRP.

          .row.align-items-center.my-3
            .col-lg-6
              p(data-aos="zoom-in-right") Existen otras metodologas, por ejemplo la propuesta hecha por Orlando Fals Borda en 1977, denominada <strong>Investigacin-Accin-Participacin (IAP)</strong>, que centraliza la persona y no el objeto de la investigacin. Otras metodologas pueden enfocarse a grupos pequeos como los familiares, es el caso de la Planificacin Predial Participativa (PPP) o a organizaciones y empresas rurales como el <strong>ndice de Capacidades Organizacionales de la Unin Europea (ICO)</strong> y la <strong>Medicin del Emprendimiento Rural (MER)</strong> de la FAO que incorporan todas las distintas reas que componen una organizacin formal.
            .col-lg-6
              p(data-aos="zoom-in-right") La gestin de la informacin, en todo caso, debe ser organizada y metdica, siguiendo la ruta datos-informacin-conocimiento; es decir, como un proceso de investigacin bsico y para ello existen diversas herramientas (de las que habla el punto cinco del ciclo de siete pasos del DRP), que pueden aplicarse en la amplitud de las actividades rurales. Algunas son muy verstiles como las entrevistas y las lluvias de ideas, otras ms especficas como los mapas de recursos, las caracterizaciones de las actividades productivas mediante mapeos y los calendarios de produccin, otras ms enfocadas a aspectos de gnero o de comunicacin, sociales u otros. 

          .row.align-items-center.grad1-2.p-3.m-auto
            .col-lg-2
              figure
                img.my-3(src='@/assets/curso/temas/tema1/img7.svg', alt='Imagen decorativa' width="64px" height="64px")
            .col-lg-7
              h4.text-light Anexo. Tabla 3
              .text-light.mb-0(style="font-size: 13px") Herramientas para el diagnstico participativo de organizaciones, empresas o unidades productivas agropecuarias. Geilfus, F. (2002).
            .col-lg-3.text-center.my-3
              a.boton(:href="obtenerLink('/downloads/anexo_tabla3.pdf')" target="_blank" type="application/pdf")
                span Descargar
