    Separador
    #t_2_6.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.6 Priorizacin de problemas a travs de la herramienta Matriz de Vester
    
    .row.my-3.align-items-center
      .col-lg-7
        p(data-aos="zoom-in-right") El objetivo del proceso de diagnstico es <strong>identificar y definir muy bien una serie de problemas de diversa ndole</strong>, donde una vez colectados, se tienen dos situaciones posibles: que se identifique fcilmente el problema central o que no se tenga claro. En el primer caso, podemos pasar al siguiente punto, el anlisis causal (causa-efecto), donde el EML nos introduce a la tcnica de rboles, la ms adecuada, aunque existan otras formas de anlisis causa-efecto.
        .cajon.color-acento-contenido.p-4(data-aos="slide-down",style="background: #FFE9DB")
          p.mt-0 En el segundo caso, donde no se tiene definido el problema central, es necesario como siguiente paso <strong>analizar la relacin entre los problemas de forma bidireccional</strong>; es decir, escudriar en la correlacin entre los distintos problemas (aqu variables) a fin de establecer el grado de influencia y/o dependencia entre ellos, el mtodo, desarrollado por el alemn <strong>Frederic Vester (1925-2003)</strong>, se fundamenta en la matemtica (teora de matrices) estableciendo un valor numrico dentro de un rango determinado que normalmente es [0-3] proporcional al grado de influencia a discrecin del analista (0 para una influencia nula o indirecta y 3 para una influencia marcada o directa).

      .col-lg-5.mt-4.col-md-8.mx-auto
        figure
          img(data-aos="zoom-in-right",src='@/assets/curso/temas/tema2/img54.png', alt='')

    .row.my-5(data-aos="zoom-in-right")
      .col-lg-12.crd.crd--avatarHorizontal-left-bgr3.py-3.mb-3
        .row.align-items-center
          .col-auto(style="z-index:1")
            figure
              img(src="@/assets/curso/temas/tema2/img55.svg", alt="", width= "100px", height="100px")
          .col.my-3(style="z-index:1")
            p.m-0 As, a la relacin entre los problemas, que son cualitativos, se le atribuye <strong>una magnitud y del ordenamiento matricial</strong> de estos valores, se puede identificar la variable (problema) con mayor influencia en relacin a las dems. En resumen, la matriz de Vester, tambin llamada matriz de <strong>influencia-dependencia (ID)</strong> nos permite <strong>identificar el problema central y tres grupos ms o menos diferenciados</strong> (causas, consecuencias y supuestos). Una consideracin importante es que la herramienta es eficaz cuando se consideran al menos 13 problemas (mnimo grado en el que adquiere significancia el tratamiento de los datos).
    
    p.text-center Antes de proseguir con la descripcin del mtodo paso por paso vale la pena enfatizar en las siguientes aclaraciones a la hora de <strong>redactar los problemas:</strong>
    
    .row.justify-content-center
      hr.mt-3.justify-content-center(style="background:#FF9751; width: 80%; height: 2px")
    .row.align-items-center.mb-3
      .col-lg-10.mx-auto.mb-auto
        .bloque-texto-b.py-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            p.mb-0 Un error que se presenta frecuentemente en la definicin del problema central, surge cuando este se describe como la falta o ausencia de una solucin frente a una necesidad experimentada por la poblacin. Este hecho sucede frecuentemente con intervenciones que conllevan la construccin de diferentes tipos de infraestructura pblica o la adquisicin de equipos. El definir la situacin problemtica de esta forma, conlleva dos debilidades en la formulacin del proyecto: se limita y condiciona la solucin a esa sola alternativa y se ignoran las condiciones que afectan realmente a la poblacin en cuanto a su bienestar y el mejoramiento de las condiciones de vida.
            h3  (DNP, 2016, p.15).
            i.fas.fa-quote-right
    
    .row.justify-content-center.mt-5 
      .col-lg-10
        .titulo-sexto.color-acento-contenido.mb-3
          h5 Figura 3.
          span <em>Recomendaciones para la correcta definicin de un problema.</em>

    .row.justify-content-center
      .col-lg-5.mb-3
        p.text-center <strong>Forma incorrecta</strong> de definir un problema
        .card(data-aos="fade-right",style="background: #FF9751 ; border-radius:20px ; height: 146px")
          .card-cuerpo.py-4
            .icon-left 
              figure
                img(src="@/assets/curso/temas/tema2/img57.svg", alt="Muestra los Aspectos para identificar problemticas, segn: La aplicacin de una poltica de desarrollo.  Ej. Agenda 2030  o el Plan Nacional. Recuperacin de infraestructura. Necesidades o carencias en la poblacin afectada. Bajos niveles de desarrollo. Condiciones de vida deficitarias. Efectos de la globalizacin de la economa y de tratados comerciales internacionales. ")
            ul.lista-ul.ms-4
              li(data-aos="fade-right", style="list-style-type: disc; color: #7F442F; font-size: 1.2em;")
                span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") Falta de un colegio.
              li(data-aos="fade-right", style="list-style-type: disc; color: #7F442F; font-size: 1.2em;")
                span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") Falta de un centro de salud.
        .my-3.ms-4
          ul.lista-ul
            li(data-aos="fade-right", style="list-style-type: disc; color:  #FF9751; font-size: 1.2em;")
              i
              strong(style="color: #FF9751") Error 1:
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") restringe la alternativa de solucin.
            li(data-aos="fade-right", style="list-style-type: disc; color:  #FF9751; font-size: 1.2em;")
              i
              strong(style="color: #FF9751") Error 2: 
              span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") no contempla ninguna condicin de desarrollo de la poblacin.
      .col-lg-5.mb-3
        p.text-center <strong>Forma correcta</strong> de definir un problema
        .card(data-aos="fade-left",style="background: #02DD84 ; border-radius:20px")
          .card-body.mx-3
            .icon-right 
              figure
                img(src="@/assets/curso/temas/tema2/img58.svg", alt="")
            ul.lista-ul.ms-4
              li(data-aos="fade-right", style="list-style-type: disc; color: #0B481E; font-size: 1.2em;") 
                span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") Bajo acceso al sistema de educacin en los niveles de bsica y media.
              li(data-aos="fade-right", style="list-style-type: disc; color: #0B481E; font-size: 1.2em;") 
                span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") Alta tasa de morbilidad infantil.
        
        .my-3.ms-4
          ul.lista-ul
              li(data-aos="fade-right", style="list-style-type: disc; color: #019A5B; font-size: 1.2em;")
                i
                strong(style="color: #019A5B") Acierto: 
                span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") para cualquiera de los casos existen diversas alternativas de solucin.
              li(data-aos="fade-right", style="list-style-type: disc; color: #019A5B; font-size: 1.2em;")
                i
                strong(style="color: #019A5B") Acierto:
                span(style="color: #12263F; font-size: 0.88em; margin: 0.5em") las dos condiciones negativas reflejan la necesidad de la poblacin.
    figcaption.justify-content-center.mb-5(style="max-width: 90%; margin: auto") Tomado de SENA (2021).
    .row.align-items-center 
      .col-lg-8.m-auto.p-4(style="background: #B3E6FF")
        p.mb-0 Hay diferentes grados de complejidad en los problemas. Desde los que pueden ser muy simples hasta aquellos donde juegan diferentes factores que hacen casi imposible su indivisibilidad. Puede ser el caso de problemas como: la pobreza, el desempleo o la inseguridad, entre otros.

    h5.my-5.text-center De acuerdo con Silva y Sandoval (2012) la construccin de la matriz de Vester para la priorizacin de problemas se detalla en los siguientes pasos:

    LineaTiempoD.color-primario.my-4
      .row(numero="1" titulo="Paso 1" )
        p Redactar, ordenar y numerar los problemas detectados en el diagnstico. (La numeracin ayudar a identificarlos fcilmente).
      .row(numero="2" titulo="Paso 2")
        .col-lg-12.mb-4.mb-md-0
          p Se procede a la construccin de la matriz <strong>enfrentando los problemas en ambos ejes</strong> como se muestra en la tabla 4. En la interseccin de los problemas contra s mismos, se pone el valor de cero. El problema debe estar redactado de manera <strong>muy precisa.</strong> 
          .titulo-sexto.color-acento-contenido.mb-3
            h5 Tabla 4.
