    #t_2_7.titulo-segundo.color-acento-contenido
      h2(data-aos="fade-left") 2.7 Anlisis causa-efecto a travs de la "metodologa de rboles"

    .row.my-5.align-items-center
      .col-lg-4.col-md-8.mx-auto
        figure
          img.my-3(src="@/assets/curso/temas/tema2/img61.png", alt="")
      .col-lg-8
        .cajon.color-secundario.p-4(data-aos="slide-down",style="background: #E4FBF2")
          p.mt-0 El problema central ya definido anteriormente es el <strong>punto de partida del anlisis a efectuar con la herramienta de rboles</strong>, aunque existan otras herramientas de anlisis causa-efecto como el mtodo del <strong>diagrama de flechas (ADM)</strong> el <strong>mtodo de la ruta crtica (CPM)</strong> el <strong>mtodo del diagrama de precedencias (PDM) la tcnica de revisin y evaluacin de programas (PERT)</strong> que son mtodos complejos usados sobre todo en anlisis en el contexto de la industria; adems, hay otros ms amigables como pueden ser el <strong>diagrama de Pareto</strong> o el <strong>diagrama de Ishikawa (espina de pescado)</strong>; de todos estos el <strong>mtodo de rboles</strong> es el que resulta, en efecto, ms conveniente para su aplicacin en el EML. 
      
    .row.my-4
      .col-lg-6
        p(data-aos="zoom-in-right") Es importante tener en cuenta que <strong>no hay un problema central definitivo o absoluto</strong> sino que depende de las capacidades y recursos del proyecto. A mayor capacidad de accin, mayor es el reto que se puede asumir y ms complejo puede ser el problema central. Lo anterior significa que <strong>no existe una manera nica de abordar una problemtica</strong> y por tanto, existen <strong>varias formas de solucionarla, distintos niveles en los que se puede actuar y un espectro ms o menos amplio en la cobertura o alcance del proyecto.</strong> Precisamente, una bondad del <strong>EML</strong> radica en tener claridad a travs de este anlisis de cmo resulta ms efectiva una intervencin; esto es ms eficaz y ms eficiente en relacin siempre a la capacidad de solucionarlo.
      .col-lg-6
        p(data-aos="zoom-in-right") Tambin podemos resumir este aporte del mtodo a <strong>una mejor manera de organizar la informacin</strong> obtenida en un momento dado para lograr orientar de manera efectiva los recursos y esfuerzos hacia un objetivo claro que nos permita obtener el mayor beneficio e impacto posibles. El rbol de problemas es, bajo el <strong>EML</strong>, la herramienta que ayuda a identificar el problema central, pero debido a que el uso de la herramienta no es fcil de abordar desde el contexto de la ruralidad, los anteriores pasos son la forma de <strong>producir insumos de calidad</strong> (anlisis de participacin de actores, identificacin y definicin previa de problemas, etc.) para el uso adecuado de esta herramienta clave.

    .row.align-items-center.my-3
      .col-auto
        img(src='@/assets/curso/temas/tema2/img3.svg', alt='Imagen decorativa')
      .col.px-0
        h3.mb-0(data-aos="zoom-in")  rbol de problemas

    .row.align-items-center.my-4
      .col-lg-8
        span La tcnica de la que hace uso esta herramienta se basa en un modelo de relaciones causales que 
        strong(style="color: #0B481E") semeja en su forma a un rbol.
        p El principio de causalidad es usado por casi toda disciplina cientfica y nos brinda unos elementos claves para el anlisis de los problemas:
        ol.lista-ol--cuadro.lista-ol--separador
          li 
            .lista-ol--cuadro__vineta(style="background: #02DD84; color: black")
              span 1
            span Los problemas son en suma acciones o productos de estas; es decir, <strong>hechos.</strong>
          li(style="display: flex; align-items: flex-start;")
            .lista-ol--cuadro__vineta(style="background: #02DD84; color: black")
              span(style="min-width: 30px; text-align: right; padding-right: 10px;") 2
            span Las acciones o hechos tienen una relacin de precedencia (en el tiempo): si A precede a B, entonces A puede ser causa de B o B es efecto de A, teniendo como condicin una proximidad en el espacio y tiempo.
        .row.justify-content-center
          .col-lg-9
            .titulo-sexto.color-acento-contenido.mb-2
              h5 Figura 5.
              span <em>Ejemplo de causa y efecto</em>
            figure
              img(src="@/assets/curso/temas/tema2/img6.svg", alt="Muestra el componente formativo un ejemplo de causa y efecto, de acuerdo: Desconocimiento de las fuentes de informacin de precios de mercado. Capacidad limitada de negociacin de bienes y*o servicios. Capacidad limitada de negociacin de bienes y*o servicios.")
              figcaption Ejemplo de causa y efecto. Bernal (2021). 
      .col-lg-4.col-md-8.mx-auto
        figure
          img.my-3(src="@/assets/curso/temas/tema2/img7.png", alt="")

    .row.align-items-center.my-4
      .col-lg-12.mb-3.m-auto
        .cajon.color-acento-contenido.p-4(data-aos="slide-down",style="background: #FFE9E1")
          p.mt-0 El ejercicio del rbol de problemas se debe efectuar en dos etapas, la primera de ellas es el <strong>establecimiento de los efectos del problema central denominado rbol de efectos</strong> A los efectos (consecuencias) directos del problema central se les esquematiza en un primer nivel hacia arriba de la grfica. A los dems en un segundo nivel estableciendo las relaciones causales entre ellos (Ortegn et.al, 2005). La gravedad de los efectos tambin es importante; en proporcin a esta se <strong>establecer la prioridad y el tipo de acciones para su solucin o mitigacin.</strong> La redaccin o el significado de lo descrito en este paso tiene una connotacin negativa, pues son problemas, esto de acuerdo con los procedimientos descritos anteriormente en este documento.
    
    .row.align-items-center.my-4
      .col-lg-10.mb-3.m-auto
        .titulo-sexto.color-acento-contenido.mb-2
          h5 Figura 6.
          span <em>Ejemplo de efectos directos e indirectos</em>
        figure
          img(src="@/assets/curso/temas/tema2/img11.svg", alt="Muestra el componente formativo un ejemplo de efectos directos e indirectos, teniendo en cuenta: Efectos directos Aumento de impactos ambientales (contaminacin fuentes y GEI). Mayor valor de la tarifa de aseo para los usuarios. Efectos Indirectos Alto uso de recursos para obtencin de materias primas. Aumento del volumen de toneladas dispuestas en relleno. Prdida de ingresos por ventas potenciales.")
          figcaption.my-3.text-regular <strong>Nota.</strong>Tomado de la gua de proyectos del DNP (2016).
    .row.align-items-center.my-4
      p En la segunda etapa se observan <strong>las causas y de la misma manera en que se procedi en la primera etapa</strong>, pero hacia abajo en la grfica, establecemos causas directas en un primer nivel y causas indirectas en un segundo e incluso un tercer nivel, estableciendo la relacin causal entre todas ellas. <strong>Es posible que una causa tenga ms de una consecuencia y viceversa.</strong> Las causas indirectas dan origen a las directas. Esta etapa es clave debido a que al hallar las causas originarias del problema de manera diferenciada, estableceremos los componentes del proyecto ms adelante. En la medida en que se contrarresten las causas (races) del problema se estar contribuyendo a la <strong>superacin del problema central.</strong>
    
    .row.align-items-center.my-4
      .col-lg-10.mb-3.m-auto
        .titulo-sexto.color-acento-contenido.mb-2
          h5 Figura 7.
          span <em>Ejemplo de causas directas e indirectas</em>
        figure
          img(src="@/assets/curso/temas/tema2/img12.svg", alt="Muestra el componente formativo un ejemplo de causas directas e indirectas, de acuerdo a : Causas directas Deficientes prcticas de separacin de materiales. Ineficientes espacios para la valorizacin de residuos slidos. Causas Indirectas Inadecuada tradicin del manejo de residuos en la fuente. Inapropiado sistema de recoleccin para el transporte selectivo. Reducida aplicacin de tecnologa en el proceso de reciclaje. Informalidad y dispersin de los recuperadores.")
          figcaption.my-3.text-regular <strong>Nota.</strong>Tomado de la gua de proyectos del DNP (2016).
    .row.align-items-center.my-4
      p Una vez identificados todos los efectos y causas del problema central, <strong>se integra toda la informacin en un solo cuadro</strong> que representa el resumen de la situacin problmica. Aunque este anlisis define el camino a seguir en adelante, los hechos descritos an mantienen el carcter de <strong>hiptesis</strong>, hasta tanto no se corroboran en los estudios del proyecto y se someten a evaluacin del equipo de trabajo y la poblacin objetivo de manera participativa <strong>(Ortegn et.al, 2005).</strong>

    .row.align-items-center.my-4
      .col-lg-10.mb-3.m-auto
        .titulo-sexto.color-acento-contenido.mb-2
          h5 Figura 8.
          span <em>Ejemplo de rbol de problemas </em>
        figure
          img(src="@/assets/curso/temas/tema2/img13.png", alt="Muestra el componente formativo un ejemplo de rbol de problemas, segn sus efectos, problemas y causas.")
          figcaption.my-3.text-regular <strong>Nota.</strong> Tomado de la gua de proyectos del DNP (2016).
        
    .row.my-3.align-items-center 
      .col-lg-12.crd.crd--avatarHorizontal-left-bgr2.py-3.mb-3
        .row.align-items-center
          .col-auto(style="z-index:1")
            figure
              img(src="@/assets/curso/temas/tema2/img62.svg", alt="", width= "100px", height="100px")
          .col(style="z-index:1")
            p.m-0 Para avanzar en la elaboracin de la propuesta se debe realizar <strong>una descripcin ms completa del problema</strong> usando todos los elementos definidos como causas y efectos, reforzando estas ideas con informacin documentada de la zona de estudio, los antecedentes del problema y de anteriores intervenciones, factores agravantes, prospectivas (estimaciones de los efectos por  la prolongacin del problema en el tiempo), condiciones actuales del problema y dems datos que refuercen la idea del problema central. El ejemplo tomado del documento oficial del DNP que usamos anteriormente reza as:
    .row.justify-content-center
      hr(style="background:#FF9751; height: 2px; width: 79%")
    .row.align-items-center.my-3
      .col-lg-10.m-auto
        .bloque-texto-b.py-5
          .bloque-texto-f__comillas
            i.fas.fa-quote-left
            i.fas.fa-quote-right
          p.bloque-texto-f__texto En el municipio XXX se generan aproximadamente 36.000 toneladas de residuos slidos al ao, con una tasa de aprovechamiento de tan solo el 1 % por parte de las familias de recuperadores presentes en la zona, segn estimaciones realizadas en el Plan Integral de Gestin de Residuos Slidos - PIGRS. <br><br>
          p.bloque-texto-f__texto Esta situacin se explica en gran medida por las inadecuadas prcticas de separacin de los residuos de los hogares, el comercio y la industria local, por la forma en la que operan las rutas de transporte para su recoleccin. Los vehculos compactadores terminan mezclando los residuos orgnicos e inorgnicos, por las restricciones de espacios adecuados y dotados con las condiciones tecnolgicas para la valorizacin de diferentes tipos de residuos. <br><br>
          p.bloque-texto-f__texto Tambin las dificultades organizativas y de intermediacin comercial con las que operan regularmente 85 personas identificadas como recuperadores informales dedicados al oficio del reciclaje. <br><br>
          p.bloque-texto-f__texto La situacin tiende a agravarse en la medida que crece anualmente la generacin de residuos en aproximadamente un 3 % anual sin que el plstico, el papel, el vidrio, los metales y los residuos orgnicos se aprovechen y se reincorporen al ciclo productivo. Esto implica una mayor explotacin de los recursos naturales para la obtencin de estas materias primas y est llevando progresivamente al agotamiento de la vida til del relleno sanitario donde se disponen los residuos con consecuencias negativas para el medio ambiente y la salud pblica. Los afluentes se ven contaminados por la escorrenta de los lixiviados de los materiales orgnicos, la atmsfera por la emisin de gases de efecto invernadero (GEI) y las personas de la zona por la presencia de vectores de transmisin de enfermedades.<br><br>
          p.bloque-texto-f__texto Adicionalmente, el aumento en la demanda del servicio de disposicin final de residuos tambin ha venido representando el incremento del 15 % en la tarifa de aseo que los usuarios del sistema tienen que pagar peridicamente segn estimaciones realizadas por la unidad de servicios pblicos domiciliarios. Tambin la prdida de ingresos que de otra forma podrn generarse gracias a la posibilidad de venta de materiales recuperados segn las cifras de comercializacin que han sido estimadas mediante el estudio de mercado que se presenta ms adelante. <strong>(DNP, 2016, p.18)</strong>.


    .row.align-items-center.my-3
      .col-lg-10.m-auto
        .cajon.color-terciario.p-4(data-aos="slide-down",style="background: #D9F3FF")
          p.mt-0  Como se puede observar, las magnitudes que hacen cuenta de factores problemticos resultan muy tiles para dimensionar el problema y sus efectos, y ms adelante sern variables susceptibles de convertirse en indicadores de gestin, tema que se abordar un poco ms adelante. 

    .row.mt-5.mb-4.align-items-center
